import React from 'react'
import "./print.css"

function PrintShop() {
  return (
    <div id='container'>
      <div id='wrapper'>
        <div id='title-box'>
          <h1 id='title'>Ate Ofie's <br /> PrintShop</h1>
        </div>
        <div id='wrapper-2'>
        <h2 id='content'>Print Rate Per Page</h2>
        <h3 id='content_p'>*print rate will depend on how many percent of the paper is printed.</h3>
        <h2 id='content_i'>Available sizes: A4, Legal, Letter etc.</h2>
        </div>
      </div>
    </div>
  )
}

export default PrintShop
